export default {
	data() {
		return {
			order:{},
			addressData: {},
		}
	},
	onShow() {
	},
	onLoad(option){
		this.initData(option);
	},
	methods: {
		initData(option){
			uni.request({
				url: this.$Url + '/clmsj/order/getOrderDetails',
				data: {
					id : option.id
				},
				header:{	'auth' : uni.getStorageSync('session_key')	},
				success: (res) => {
					console.log(res.data.data);
					this.order = res.data.data;
					let {stateTip, stateTipColor} = this.orderStateExp(this.order.state);
					console.log({stateTip, stateTipColor})
					console.log(this.order)
					this.order = Object.assign(this.order, {
						stateTip, 
						stateTipColor
					}) 
					console.log(this.order)
				}
			});
		},
		payment(order_sn){
			uni.request({
				url: this.$Url + '/clmsj/order/getPayParam',
				data: {
					order_sn: order_sn
				},
				header:{ 'auth' : uni.getStorageSync('session_key') },
				success: (resp) => {
					console.log(resp);
					let res = resp.data.data;
					// 调起支付
					uni.requestPayment({
						provider: 'wxpay',
						timeStamp: res.timeStamp,
						nonceStr: res.nonceStr,
						package: res.package,
						signType: 'MD5',
						paySign: res.paySign,
						success: (res) => {
							uni.showToast({
								title:'支付成功'
							});
							this.initData({id:this.order.id});
						},
						fail: (err)=> {
							console.log('fail:' + JSON.stringify(err));
							uni.showToast({
								title:'支付失败'
							})
						}
					});
				}
			});
		},
		//订单操作
		handleOrder(item,state){
			let tipMsg = '您确认要进行该操作吗？';
			if(state == 9){
				tipMsg = '您确认要取消订单吗？';
			}else if(state==4){
				tipMsg = '请确认您已收到货';
			}
			uni.showModal({
			    title: '提示',
			    content: tipMsg,
			    success: (resC=>{
					if(resC.confirm){
						uni.showLoading({
							title: '请稍后'
						})
						let {stateTip, stateTipColor} = this.orderStateExp(state);
						item = Object.assign(item, {
							state: state,
							stateTip, 
							stateTipColor
						})
						uni.request({
							url: this.$Url + '/clmsj/order/userUpdateOrderStatus', // http://bull.hnw.net/index/bull/client_wash_info
							data: {
								id:item.id,
								state:state
							},
							header:{ 'auth' : uni.getStorageSync('session_key') },
							method:'POST',
							success: (res) => {
								uni.hideLoading();
								if (res.data.code == 0) {
									this.order.state = state;
									this.$api.msg('操作成功');
								}
							},
						})
						uni.hideLoading();
					}
					
				})
			})
		},
		//查看物流
		viewExpress(item){
			console.log('viewExpress')
			uni.navigateToMiniProgram({
				appId: 'wx6885acbedba59c14',
				path: 'pages/result/result?nu='+ item.express_number +'&com=&querysource=third_xcx',
				success(res) {
					// 打开成功
				}
			})
		},
		//订单状态文字和颜色
		orderStateExp(state){
			let stateTip = '',
				stateTipColor = '#ffffff';
			switch(+state){
				case 1:
					stateTip = '待付款，请尽快支付'; break;
				case 2:
					stateTip = '已付款，待发货'; break;
				case 3:
					stateTip = '已发货，待收货'; break;
				case 4:
					stateTip = '已收货'; break;
				case 9:
					stateTip = '订单已关闭'; 
					stateTipColor = '#ffffff';
					break;
			}
			return {stateTip, stateTipColor};
		}
	}
}
